interface IConfig
{
  VUE_APP_BACKEND_HOST_AND_PORT: string | undefined;
}

if (!process.env.VUE_APP_BACKEND_HOST_AND_PORT) {
  throw new Error(
    `BACKEND_HOST_AND_PORT doesn't defined", ${process.env.VUE_APP_BACKEND_HOST_AND_PORT}`
  )
}

export const AppConfig: IConfig = {
  VUE_APP_BACKEND_HOST_AND_PORT: process.env.VUE_APP_BACKEND_HOST_AND_PORT
}
