import { useApiStore } from '../../store'
import { PersonalInfo } from '../types'
import { getCookie } from '@/api/utils/cookies'

export const getUserInfo = function (): PersonalInfo {
  return useApiStore.api.items.personalInfo
}

export const isAuthenticated = async function (): Promise<boolean> {
  return !!getCookie('XSRF-TOKEN')
}
