// api
import { ApiCalcReplicateGet, ApiCalcReplicatePost } from '@/api/endpoints/calc'
import { CalcReplicateGetReponse } from '../types'

const StoreApiCalcReplicateGet = async function () {
  const response: CalcReplicateGetReponse = await ApiCalcReplicateGet()
  this.$state.api.items.calcInfo = response
}

// todo ... Nikolay Lytvyn ... will be here more correct name
const StoreApiCalcReplicatePost = async function () {
  await ApiCalcReplicatePost()

  const response: CalcReplicateGetReponse = await ApiCalcReplicateGet()
  this.$state.api.items.calcInfo = response
}

export const CalcApiActions = {
  StoreApiCalcReplicateGet: StoreApiCalcReplicateGet,
  StoreApiCalcReplicatePost: StoreApiCalcReplicatePost
}
