<script lang="ts">
import { defineComponent } from 'vue'
import SvgIcon from '@jamescoyle/vue-icon'
import type { PropType } from 'vue'

export interface Link {
  name: string;
  path: string;
  image?: string;
}

export default defineComponent({
  name: 'index',

  components: {
    SvgIcon
  },

  props: {
    title: {
      type: String,
      required: true
    },
    links: {
      type: [] as PropType<Array<Link>>,
      required: true
    }
  },
  computed: {
    showHideAttr: function (): string {
      return this.isShowLinks ? 'mdi-plus' : 'mdi-minus'
    }
  },

  data: function () {
    return {
      isShowLinks: true
    }
  },

  methods: {
    showHide: function () {
      this.isShowLinks = !this.isShowLinks
    }
  }
})
</script>

<template>

  <v-list>
    <router-link :key="link.name" v-for="link in links" :to="link.path" style="text-decoration: none; color: inherit">
      <v-list-item style="background: 1px solid red;" :title="link.name" :value="link.path">
        <template v-slot:prepend>
          <div>
            <svg-icon type="mdi" :path="link.image || ''"></svg-icon>
          </div>
        </template>
      </v-list-item>
    </router-link>
  </v-list>
</template>

<style scoped lang="stylus">
.title {
  cursor pointer;
}

.links {
  padding-left: 20px
}
</style>
