<template>
  <!-- Errors section -->
  <v-alert border="top" color="red lighten-2" dark v-if="showGlobalApiError">
    <v-container v-for="item in globalApiError" :key="item.id">
      <v-row>
        <v-col cols="7">
          {{ item.config.url}}
        </v-col>

        <v-col cols="4">
          {{ item.message}}
        </v-col>

        <v-col cols="1" align="right">
          <div @click="removeGlobalApiError" style="cursor: pointer">
            <v-icon icon="$delete"></v-icon>
          </div>
        </v-col>
      </v-row>
    </v-container>
  </v-alert>

  <!-- Spinner sections -->
  <v-app>
    <v-main>
      <router-view />
    </v-main>
  </v-app>
</template>

<script lang="ts">
import { defineComponent } from 'vue'
import { AxiosError } from 'axios'

// store
import { useApiStore } from '@/stores/apiStore'
import { getGlobalErrors } from '@/stores/apiStore/global-requests-state/queries'

export default defineComponent({
  name: 'App',

  mounted () {
    console.log('start')
  },
  computed: {
    globalApiError (): Array<AxiosError<unknown, unknown>> {
      return getGlobalErrors()
    },
    showGlobalApiError: function (): boolean {
      return !!Object.keys(getGlobalErrors()).length
    }
  },
  methods: {
    removeGlobalApiError: function () {
      useApiStore.removeApiError()
    }
  }
})
</script>
