import axios from '../../axios'
import { CalcReplicateGetReponse } from './types'

export const ApiCalcReplicateGet = async (): Promise<CalcReplicateGetReponse> => {
  try {
    const url = '/calc-replicate'
    const result = await axios.get(url)

    return result.data as CalcReplicateGetReponse
  } catch (err) {
    return Promise.reject(new Error(err))
  }
}

export const ApiCalcReplicatePost = async (): Promise<CalcReplicateGetReponse> => {
  try {
    const url = '/calc-replicate'
    const result = await axios.post(url, {
      name: 'start'
    })
    return result.data as CalcReplicateGetReponse
  } catch (err) {
    return Promise.reject(new Error('something bad happened'))
  }
}
