import { ROUTER_PATH } from '@/router/types'
import {
  NavigationGuardNext,
  RouteLocationNormalized
} from 'vue-router'
import { isAuthenticated } from '@/stores/apiStore/auth/queries'

const defaultUnprotectedRoute = ROUTER_PATH.welcome.name

const isAllowNextRouteTransition =
  async (to: RouteLocationNormalized): Promise<boolean> => {
    return to.name !== defaultUnprotectedRoute && !await isAuthenticated()
  }

export const beforeEachRouterHook = async (to: RouteLocationNormalized, from: RouteLocationNormalized, next: NavigationGuardNext) => {
  if (await !isAllowNextRouteTransition(to)) {
    next({ name: defaultUnprotectedRoute })
  }

  // todo ... Nikolay Lytvyn ... will be here механизм запрещающий переходить на welcome , если пользователь залог
  else next()
}
