import axios from '../../axios'

export interface MeResponse {
  city: string;
  department: string;
  givenName: string;
  familyName: string;
  avatar: string;
}

export const MeGet = async (): Promise<MeResponse> => {
  try {
    const url = '/me'
    const result = await axios.get(url)

    // todo ... Nikolay Lytvyn ... will be here
    result.data.avatar = 'https://svgsilh.com/png-1024/1363011.png'

    return result.data as MeResponse
  } catch (err) {
    return Promise.reject(new Error('something bad happened'))
  }
}
