import { MeGet, MeResponse } from '@/api/endpoints/me'
import { KeyCloackApiLogin, KeyCloackApiLogout } from '@/api/endpoints/keycloack'
import { RedirectToWelcomePage } from '@/router/redirects'

import { IStoreMethods } from '../types'

const StoreLogin = async function () {
  await KeyCloackApiLogin()

  await RedirectToWelcomePage()
}

const StoreSavePersonInfo = async function () {
  const response: MeResponse = await MeGet()

  this.$state.api.items.personalInfo.fullName = `${response.familyName} ${response.givenName}`
  this.$state.api.items.personalInfo.avatar = response.avatar
  this.$state.api.items.personalInfo.department = response.department
}

export const StoreLogout = async function () {
  KeyCloackApiLogout()
  this.$state.personalInfo = {}
}

export const storeActions:IStoreMethods = {
  StoreLogin: StoreLogin,
  StoreLogout: StoreLogout,
  StoreSavePersonInfo: StoreSavePersonInfo
}
