import { RouteRecordRaw } from 'vue-router'
import { ROUTER_PATH } from '@/router/types'

// routes
import TechnicalBoard from '@/views/technical-board/index.vue'

import Requirments from '@/views/technical-board/components/requirments/index.vue'
import Stands from '@/views/technical-board/components/stands/index.vue'
import Swagger from '@/views/technical-board/components/swagger/index.vue'
import ReportsInfo from '@/views/technical-board/components/tests/index.vue'

export const TechnicalBoardRoutes: RouteRecordRaw = {
  path: ROUTER_PATH.technicalBoard.path,
  name: 'technicalBoard',
  component: TechnicalBoard,
  redirect: { path: '/technicalBoard/requirments' },
  children: [
    {
      path: ROUTER_PATH.requirments.path,
      component: Requirments,
      name: 'requirments'
    },
    {
      path: ROUTER_PATH.stands.path,
      component: Stands,
      name: 'stands'
    },

    {
      path: ROUTER_PATH.swagger.path,
      component: Swagger,
      name: 'swagger'
    },
    {
      path: ROUTER_PATH.additionalInfo.path,
      component: ReportsInfo,
      name: 'tests'
    }
  ]
}
