import {
  createRouter,
  createWebHistory,
  RouteRecordRaw
} from 'vue-router'

//  views
import Welcome from '../views/welcome/index.vue'
import NotFoundPage from '@/views/404page/index.vue'

// routes
import { TechnicalBoardRoutes } from '@/router/routers/technical-board'

import { beforeEachRouterHook } from '@/router/router-hooks'

export const routes: Array<RouteRecordRaw> = [
  {
    path: '',
    redirect: { name: 'defaultBoardRoute' }
  },

  {
    path: '/:pathMatch(.*)*', component: NotFoundPage
  },

  {
    path: '/welcome',
    name: 'defaultBoardRoute',
    component: Welcome
  },

  TechnicalBoardRoutes

]

const router = createRouter({
  history: createWebHistory(),
  routes
})

router.beforeEach(
  async (to, from, next) => {
    await beforeEachRouterHook(to, from, next)
  })

export default router
