import axios, { AxiosError, AxiosResponse } from 'axios'

import { AppConfig } from '@/config'

// store
import { useApiStore } from '@/stores/apiStore'

// routing
import { RedirectToWelcomePage } from '@/router/redirects'
const { VUE_APP_BACKEND_HOST_AND_PORT } = AppConfig

const instance = axios.create({
  baseURL: VUE_APP_BACKEND_HOST_AND_PORT + '/bff'
})

export const AuthMechanism = (error: AxiosError) => {
  // todo ... Nikolay Lytvyn ... will be here уточнить пор условие проверки ло config.url
  // @ts-ignore
  if (error.code === 'ERR_BAD_REQUEST') {
    RedirectToWelcomePage()
  }
}

/**
 * Requests
 * */
instance.interceptors.request.use(
  function (config) {
    useApiStore.startExecuteRequest()
    return config
  },
  function (error: Error) {
    return Promise.reject(error)
  }
)

/**
 * Responses
 * */
instance.interceptors.response.use(
  function (response: AxiosResponse) {
    useApiStore.endExecuteRequest()
    return response
  },
  function (error: AxiosError) {
    /**
     * seamless authorization */
    AuthMechanism(error)

    // todo ... Nikolay Lytvyn ... will be here доработать условия проверки статусов ( не отображать на 300е)
    useApiStore.endExecuteRequest()

    if (error.status !== 401) {
      useApiStore.setApiError({ error })
    }

    return Promise.reject(error)
  }
)

export default instance
