<template src="./index.html"></template>
<style src="./index.styl" scoped lang="stylus"></style>

<script lang='ts'>
import { defineComponent } from 'vue'

import {
  api
} from './api'

interface Item {
  id: string;
  title: string;
}

export default defineComponent({
  name: 'App',

  setup () {
    return api.reuse()
  },

  data () {
    return {
      items: [] as Array<Item>
    }
  },
  methods: {
    addItem: async function (e: Event) {
      const { target } = e
      const value = (target as HTMLButtonElement).value
      await this.addItemExample({ title: value, description: 'description' })
      this.getItems();

      (target as HTMLButtonElement).value = ''
    },
    removeItem: async function (id: string) {
      await this.removeItemExample({ id })
      await this.getItems()
    }
  }
})
</script>
