import { AxiosError } from 'axios'

import { AppConfig } from '@/config'

// utils
import { getCookie } from '../../utils/cookies'

export const KeyCloackApiLogin = async (): Promise<boolean | AxiosError> => {
  try {
    const { VUE_APP_BACKEND_HOST_AND_PORT } = AppConfig
    window.location.href = `${VUE_APP_BACKEND_HOST_AND_PORT}/bff/oauth2/authorization/keycloak`

    return Promise.resolve(true)
  } catch (err) {
    console.error('err', err)
    return Promise.resolve(err as AxiosError)
  }
}

export const KeyCloackApiLogout = async (): Promise<boolean | AxiosError> => {
  try {
    const { VUE_APP_BACKEND_HOST_AND_PORT } = AppConfig
    const url = `${VUE_APP_BACKEND_HOST_AND_PORT}/bff/logout`

    const tokenKey = 'XSRF-TOKEN'
    const tokenValue = getCookie(tokenKey)

    const CSRF_QUERY_PARAM_KEY = '_csrf'

    window.location.href = `${url}?${CSRF_QUERY_PARAM_KEY}=${tokenValue}`
    return Promise.resolve(true)
  } catch (err) {
    console.error('err', err)
    return Promise.resolve(err as AxiosError)
  }
}
