import { AxiosError } from 'axios'
import { IStoreActions } from '../types'

export const storeActions: IStoreActions = {
  setApiError (data: { error: AxiosError }) {
    this.$state.requestState.errors.push(data.error)
  },
  removeApiError () {
    this.$state.requestState.errors = []
  },
  startExecuteRequest () {
    this.$state.requestState.isLoading = true
  },
  endExecuteRequest () {
    this.$state.requestState.isLoading = false
  }
}
