import { computed } from 'vue'
import gql from 'graphql-tag'
import { MutateFunction, useMutation, useQuery } from '@vue/apollo-composable'

export const schemaExampleGetItems = gql`query{
   exampleGetItems(skip: 0, take: 50){
    id,
    title
  }
}`

export const schemaExampleAddItem = gql`mutation exampleAddItem($title: String!, $description: String!) {
  exampleAddItem(item: { title: $title, description: $description }) {
    title
    description
  }
}`

export const schemaRemoveItem = gql`mutation exampleRemoveItem($id: String!){
 exampleRemoveItem(id: $id)
}`
export const api = {
  reuse: function ():
      {
      items: unknown,
      getItems: unknown,
      addItemExample: MutateFunction<unknown, { title: string; description: string; }>
        removeItemExample: MutateFunction<unknown, { id: string;}>
          // addItemExample: any
        } {
    //   @ts-ignore
    const { result, refetch: getItems } = useQuery(schemaExampleGetItems)
    const { mutate: addItemExample } = useMutation(schemaExampleAddItem)
    const { mutate: removeItemExample } = useMutation(schemaRemoveItem)

    const items = computed(() => result.value?.exampleGetItems ?? [])
    return {
      // queries
      items,
      getItems,

      // mutations
      addItemExample,
      removeItemExample
    }
  }
}
